import React from "react";
import {connect} from "react-redux";
import {setSelectedVariant, addToCart, setChosenImageIndex} from "eshop/modules/simfree/actions/app";
import {getProductName, getSelectedVariant, getProductImageUrl} from "eshop/modules/simfree/selectors";
import {OraButton} from "eshop/components/OraCommonComponents";
import ProductGalleryComponent from "eshop/modules/simfree/components/gallery/ProductGalleryComponent";
import ProductDetailsNavigationComponent
    from "eshop/modules/simfree/components/details/ProductDetailsNavigationComponent";
import ProductDetailsProductDescription
    from "eshop/modules/simfree/components/details/ProductDetailsProductDescription";
import ProductDetailsProductSpecification
    from "eshop/modules/simfree/components/details/ProductDetailsProductSpecification";
import ProductRecomendation from "eshop/modules/simfree/components/details/ProductRecomendation";
import ProductCartProductReviewComponent
    from "eshop/modules/simfree/components/details/ProductCartProductReviewComponent";
import DataLayerUtils from "eshop/utils/DataLayerUtils";
import ProductListHeaderComponent from "eshop/modules/simfree/components/list/ProductListHeaderComponent";
import OraStockLevelStatusComponent from "eshop/components/OraStockLevelStatusComponent"
import ProductDetailsProductPromotion from "./ProductDetailsProductPromotion";
import {getAssistModeStateData} from "../../../checkout/selectors";
import {getMarketContext} from "../../../configurator/selectors/filters";

class ProductDetailsView extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.props.setSelectedVariant({productId: this.props.deviceData.selectedVariant});
        this._loadModules();

        DataLayerUtils.pushPageCategoryDimension('TelefonyBezUmowy');
        DataLayerUtils.pushSimfreeProductImpression(this.findProductVariant(), this.props.deviceData.price,null,this.props.marketContext, this.props.channels.sales, {loggedEmployee : this.props.assistedModeStateData, assistedEmployee : this.props.assistedModeStateData});
    }

    findProductVariant() {
        for (var i = 0; i < this.props.deviceData.variantList.length; i++) {
            if (this.props.deviceData.variantList[i].productId == this.props.selectedVariant
                || (!this.props.selectedVariant && this.props.deviceData.variantList[i].productId == this.props.deviceData.selectedVariant)) {
                return this.props.deviceData.variantList[i];
            }
        }
    }

    handleSelectVariant(variant, color) {
        this.props.setSelectedVariant(variant, color);
        this.props.setChosenImageIndex(0);
    }

    render() {
        return (
            <div className="opl-product-page">
                <div className="kss-layout-coloring kss-resolution-large">
                    <div className="l-full-row">
                        <div className="l-page-row u-small-no-padding u-medium-no-padding">
                            <div className="u-spacing-top-xl u-spacing-xl u-small-spacing-l">

                                <div
                                    className="l-col l-col-small-12 l-col-medium-12  u-large-hide u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                                    <h2 data-skiplinks="Podstawowe informacje o produkcie"
                                        className="h1 u-medium-no-spacing" tabIndex="-1"
                                        id="skiplinks831">{this.props.deviceData.name}</h2>
                                </div>

                                <div className="l-row u-small-no-spacing u-medium-no-spacing">
                                    <div
                                        className="l-col l-col-small-12 l-col-medium-12 l-col-6  u-text-center u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l u-position-static">
                                        <ProductGalleryComponent productVariant={this.findProductVariant()}
                                                                 sticker={this.props.deviceData.sticker}/>
                                    </div>
                                    <div className="l-col l-col-small-12 l-col-medium-12 l-col-6 "
                                         id="product-basic-information">
                                        <div className="l-row">
                                            <div
                                                className="l-col l-col-medium-12 l-col-12  u-small-hide u-medium-hide u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                                                <h1 className="h1 u-spacing">{this.props.deviceData.name}</h1>
                                            </div>
                                            <div
                                                className="l-col l-col-medium-6 l-col-8  u-small-hide u-medium-hide u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                                                <div className="opl-rating"><span
                                                    className="g-icon g-icon--star g-icon--s"></span><span
                                                    className="g-icon g-icon--star g-icon--s"></span><span
                                                    className="g-icon g-icon--star g-icon--s"></span><span
                                                    className="g-icon g-icon--star g-icon--s"></span><span
                                                    className="g-icon g-icon--star g-icon--s"></span>
                                                    <div style={{width: this.props.deviceData.percentageRating}}
                                                         className="opl-rating-bar"><span
                                                        className="g-icon g-icon--star g-icon--s"></span><span
                                                        className="g-icon g-icon--star g-icon--s"></span><span
                                                        className="g-icon g-icon--star g-icon--s"></span><span
                                                        className="g-icon g-icon--star g-icon--s"></span><span
                                                        className="g-icon g-icon--star g-icon--s"></span></div>
                                                </div>
                                                <p className="small g-gray3-c u-padding-left-s u-inline-block">({this.props.deviceData.numberOfReviewsCustomers})</p>
                                                <p className="u-acc-hide">{this.props.deviceData.numberOfReviewsCustomers} opini</p>
                                            </div>
                                            <ProductListHeaderComponent/>
                                            {this._renderSeparator()}
                                            <div
                                                className="l-col l-col-small-12 l-col-medium-8 l-col-12  u-small-padding-l u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                                                <div className="l-row u-display_table">
                                                    <div className="l-col l-col-3  u-display_table-cell">
                                                        Kolor: <span
                                                        className="u-font-bold">{this.findProductVariant().colorDefinition[0].name}</span>
                                                    </div>
                                                    <div className="l-col l-col-9  u-display_table-cell">
                                                        <div className="opl-color-chooser">
                                                            <fieldset>
                                                                <legend className="u-acc-hide">Kolor telefonu</legend>
                                                                {this._renderColorChooserOptions()}
                                                            </fieldset>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            {this._renderSeparator()}
                                            <div
                                                className="l-col l-col-12  u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                                                <p className="opl-box__price">
                                                    <span className="opl-box__header">
                                                        {this.findProductVariant().devicePaymentsData.oneTimePayment.price}
                                                    </span>
                                                    <span className="h5">zł</span>
                                                </p>
                                            </div>
                                            {this._renderSeparator()}
                                            {this._renderDeliveryInformation()}
                                            {this._renderMobileButton()}
                                            {this._renderNonMobileButton()}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="l-full-row">
                    <ProductDetailsNavigationComponent/>
                    <div className="l-page-row">
                        <div id={"opl-expander-product-details"} data-js-module={'common/modules/opl-expander'}
                             role={'tablist'} aria-multiselectable={false}>
                            <ProductDetailsProductPromotion promotion={this.props.deviceData.promotion}/>
                            <ProductDetailsProductDescription description={this.props.deviceData.description}/>
                            <ProductDetailsProductSpecification deviceData={this.props.deviceData}/>
                            <ProductRecomendation products={this.props.recomendedProducts} productId={this.props.deviceData.productId}/>
                            <ProductCartProductReviewComponent deviceData={this.props.deviceData}/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    _renderColorChooserOptions() {
        return this.props.deviceData.variantList.map((variant) => {
            return (
                <label className="opl-color-chooser__item" key={"color-chooser-" + variant.productId}>
                    <input type="radio" name={"color_" + this.props.deviceData.productId}
                           value={variant.productId}
                           checked={variant.productId == this.findProductVariant().productId}
                           onChange={this.handleSelectVariant.bind(this, variant, variant.colorUrl)}/>
                    <span className="opl-color-chooser__color"
                          style={{background: variant && variant.colorDefinition && variant.colorDefinition[0] && variant.colorDefinition[0].cssCode && variant.colorDefinition[0].cssCode.replace(/\'/g, "")}}>
                    </span>
                    <span
                        className="opl-color-chooser__label">{variant && variant.colorUrl}</span>
                </label>
            );
        }, this);
    }

    _renderSeparator() {
        return (
            <div
                className="l-col l-col-12  u-padding-l u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                <div className="o-separator u-padding-top-l"></div>
            </div>
        )
    }

    _loadModules() {
        OPL.UI.loadModules(document.getElementById("opl-expander-product-details"), [{
            path: 'common/modules/opl-expander',
            options: {
                'scrollToSelected': true,
                'hideOtherElements': true
            },
            conditions: 'media:{(max-width:767px)}'
        }]);
        OPL.UI.loadModules(document.getElementById("zoom-container-image"), [{
            path: 'common/modules/opl-image-zoom',
            conditions: 'media:{gt-medium}',
            options: {
                zoomWindowOffsetY: 70,
                zIndex: 3,
                zoomContainerAppendTo: '.opl-product-page',
                zoomWindowOffsetXAlignContainer: '#product-basic-information',
                zoomWindowWidthAlignContainer: '#product-basic-information'
            }
        }]);
    }

    _renderDeliveryInformation() {
        let element = null;
        if (!OraStockLevelStatusComponent.isAvailableInStock(this.findProductVariant().stockLevelStatus)) {
            element = <div
                className="l-col l-col-small-12 l-col-medium-8 l-col-6 u-medium-padding-left-l u-medium-padding-right-l">
                <div className="u-display_table u-w-auto u-padding-s">
                    <OraStockLevelStatusComponent stockLevelStatus={this.findProductVariant().stockLevelStatus}
                                                  stockLevel={this.findProductVariant().stockLevel}
                                                  stockLevelDate={this.findProductVariant().stockLevelDate}
                                                  shouldStockLevelBeVisible={this.findProductVariant().shouldStockLevelBeVisible}/>
                </div>
                <div className="u-display_table u-w-auto u-padding-s u-padding-top-l">
                    <p className="u-display_table-cell g-icon g-icon--delivery2 g-icon--s u-padding-right-m"></p>
                    <p className="u-display_table-cell">
                        <span className="u-padding-right-s">{this.props.descriptions.deliveryLabel}&nbsp;</span>
                        <span className="u-padding-left-s u-font-bold">niedostępne</span>
                    </p>
                </div>
            </div>
        } else {
            element = <div
                className="l-col l-col-small-12 l-col-medium-8 l-col-6  u-medium-padding-left-l u-medium-padding-right-l">
                <div className="u-display_table u-w-auto u-padding-s">
                    <OraStockLevelStatusComponent stockLevelStatus={this.findProductVariant().stockLevelStatus}
                                                  stockLevel={this.findProductVariant().stockLevel}
                                                  stockLevelDate={this.findProductVariant().stockLevelDate}
                                                  shouldStockLevelBeVisible={this.findProductVariant().shouldStockLevelBeVisible}/>
                </div>
                <div className="u-display_table u-w-auto u-padding-s">
                    <p className="u-display_table-cell g-icon g-icon--boutique g-icon--s u-padding-right-l"></p>
                    <p className="u-display_table-cell">
                        <span className="u-padding-right-s">{this.props.descriptions.deliveryLabel}&nbsp;</span>
                        <span className="u-padding-left-s">{this.props.descriptions.deliveryValue}</span>
                    </p>
                </div>
            </div>;
        }
        return element;
    }

    _renderMobileButton() {
        let element = null;
        if (!OraStockLevelStatusComponent.isAvailableInStock(this.findProductVariant().stockLevelStatus)) {
            //element = <div className="l-col l-col-small-12 u-large-hide u-medium-hide u-small-padding-all-l">
            //    <a className="o-btn opl-btn opl-btn--medium u-w-100 o-modal__trigger"
            //       href="#availability-modal"
            //       data-js-module="core/modules/modal"
            //       data-js-options={JSON.stringify({"modalClass": "o-modal o-modal--small u-padding-all-l"})}>
            //        Poinformuj o dostępności
            //    </a>
            //    {this._renderAvailabilityModal()}
            //</div>
            element = ""
        } else {
            element = <div className="l-col l-col-small-12 u-large-hide u-small-padding-all-l">
                <OraButton type="primary"
                           className="o-btn opl-btn opl-btn--primary opl-btn--medium u-w-100"
                           disabled={!OraStockLevelStatusComponent.isAvailableInStock(this.findProductVariant().stockLevelStatus)}
                           onClick={this.handleAddToCartMobileUX.bind(this, this.findProductVariant().productId, this.props.deviceData.bundleTemplateName)}>
                    Do koszyka
                </OraButton>
            </div>
        }
        return element;
    }

    _renderNonMobileButton() {
        let element = null;
        if (!OraStockLevelStatusComponent.isAvailableInStock(this.findProductVariant().stockLevelStatus)) {
            //element = <div className="l-col l-col-small-12 l-col-medium-4 l-col-6 u-medium-padding-left-l u-medium-padding-right-l u-small-hide">
            //    <a className="o-btn opl-btn opl-btn--medium u-w-100 o-modal__trigger"
            //       href="#availability-modal"
            //       data-js-module="core/modules/modal"
            //       data-js-options={JSON.stringify({"modalClass": "o-modal o-modal--small u-padding-all-l"})}>
            //        Poinformuj o dostępności
            //    </a>
            //    {this._renderAvailabilityModal()}
            //</div>
            element = ""
        } else {
            element = <div
                className="l-col l-col-small-12 l-col-medium-4 l-col-4  u-medium-padding-left-l u-medium-hide u-small-hide">
                <OraButton type="primary"
                           className="o-btn opl-btn opl-btn--primary opl-btn--medium u-w-100"
                           disabled={!OraStockLevelStatusComponent.isAvailableInStock(this.findProductVariant().stockLevelStatus)}
                           onClick={this.handleAddToCart.bind(this, this.findProductVariant().productId, this.props.deviceData.bundleTemplateName)}>
                    Do koszyka
                </OraButton>
            </div>
        }
        return element;
    }

    _renderAvailabilityModal() {
        return (
            <div id="availability-modal" className="u-hide--soft">
                <p className="h3 u-spacing-xl">{this.props.deviceData.name}</p>
                <div className="opl-product-availability-modal__picture"><img src={this.findProductVariant().imageUrl}/>
                    <div className="opl-product-box__unavailable-banner u-no-bg">
                        <p className="u-font-bold u-font-small u-text-center">Produkt chwilowo niedostępny</p>
                    </div>
                </div>
                <p className="u-spacing-top-l u-spacing-l u-font-bold">Podaj adres email, na który powiadomimy Cię o
                    dostępności produktu</p>
                <input type="text" placeholder="Adres email" className="opl-input--size-m u-spacing-l"/>
                <label className="o-checkbox opl-checkbox u-spacing-l">
                    <input type="checkbox"/>
                    <span className="o-ci"></span>
                    <span className="o-ci-label u-font-normal">Wyrażam zgodę na przesyłanie informacji handlowych środkami komunikacji elektronicznej (sms, email)</span>
                </label>
                <a className="o-btn opl-btn opl-btn--primary opl-btn--medium u-right u-padding-right-xxl u-padding-left-xxl u-spacing-l"
                   rel="modal:close">Wyślij</a>
            </div>
        );
    }

    handleAddToCart(productId, bundleTemplateName) {
        DataLayerUtils.pushSimfreeAddToCart(this.findProductVariant(), this.props.deviceData.price,this.props.marketContext, this.props.channels.sales, {loggedEmployee : this.props.assistedModeStateData, assistedEmployee : this.props.assistedModeStateData});
        this.props.addToCart(productId, bundleTemplateName);
    }

    handleAddToCartMobileUX(productId, bundleTemplateName) {
        DataLayerUtils.pushSimfreeAddToCart(this.findProductVariant(), this.props.deviceData.price,this.props.marketContext, this.props.channels.sales, {loggedEmployee : this.props.assistedModeStateData, assistedEmployee : this.props.assistedModeStateData});
        this.props.addToCart(productId, bundleTemplateName, this.props.multicartUrl);
    }

}

const mapStateToProps = state => ({
    productName: getProductName(state),
    selectedVariant: getSelectedVariant(state),
    imageUrl: getProductImageUrl(state),
    assistModeStateData : getAssistModeStateData(state),
    marketContext: getMarketContext(state)
});

const mapDispatchToProps = (dispatch, props) => ({
    setSelectedVariant: (selectedVariantCode, colorCode) => dispatch(setSelectedVariant(selectedVariantCode, colorCode, props.filterState)),
    addToCart: (productCode, bundleTemplateName, redirectUrl) => dispatch(addToCart(productCode, bundleTemplateName, redirectUrl)),
    setChosenImageIndex: (index) => dispatch(setChosenImageIndex(index))
});

const ProductDetailsComponent = connect(mapStateToProps, mapDispatchToProps)(ProductDetailsView);
export default ProductDetailsComponent;